.completed-challenges-list{
    margin-top: 1em;
    display: flex;
    flex-direction: column;
    gap: 0.25em;
    max-height: 300px;
    overflow-y: scroll;
    padding-right: 8px;
    min-width: 340px;
    min-height: 200px;
    &::-webkit-scrollbar {
        width: 6px;
    }
    &::-webkit-scrollbar-track {
        background: #FFFFFFA0;
    }
    &::-webkit-scrollbar-thumb {
        background: #FFF;
    }
}
@media (max-width: 1023px){
    .completed-challenges-list{
        gap: 0.75em;
    }
}
@media (max-width: 560px){
    .completed-challenges-title{
        font-size: 32px;
        text-align: center;
    }
    .completed-challenges-list{
        gap: 4px;
        min-width: auto;
        min-height: 140px;
    }
}